import "./Login.css";
import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { Link, useNavigate } from 'react-router-dom';
import { REGEXP_EMAIL } from "../../components/utils/constants";
import  fetchUserLogin from "../../components/utils/fetchUserLogin";

function Login() {
  const navigate = useNavigate();
  const [isActive, setIsActive] = useState(false);

  const {
    register,
    handleSubmit,
    formState: { errors, isValid },
  } = useForm({
    defaultValues: {
      email: '',
      password: '',
    },
    mode: 'onChange',
  });

  const onSubmit = (data) => {
    fetchUserLogin({
      email: data.email,
      password: data.password
    })

    // .then((res) => { // ,   baseUrl
    //   if (res.ok) {
    //     navigate('/');
    //   }
    // })
  }

  function onHandleClick() {
    setIsActive((current) => !current);
  }

  return (
    <div className="login__wrapper">
      <form
        id="login_form"
        onSubmit={handleSubmit(onSubmit)}
        className="login__form"
        action="submit"
        noValidate
      >
        <h1 className="login__title">CRM </h1>
        <h2 className="login__subtitle">  </h2>
        <div className="login__inputWrapper">
          <input
            {...register('email', {
              required: '    ',
              pattern: {
                value: REGEXP_EMAIL,
                message: '    ',
              },
            })}
            className={`login__input ${errors.email ? 'login__inputInvalid' : ''}`}
            type="email"
            name="email"
            id="email"
            placeholder=""
          />
          <span className="login__error">{errors?.email && errors.email.message}</span>
        </div>
        <div className="login__inputWrapper">
          <input
            {...register('password', {
              required: '    ',
              minLength: {
                value: 8,
                message: '     8 ',
              },
              maxLength: {
                value: 16,
                message: '     16 ',
              },
            })}
            className={`login__input ${errors.password ? "login__inputInvalid" : ''}`}
            type={isActive ? 'text' : 'password'}
            name="password"
            id="password"
            placeholder=""
          />
          <button
              type="button"
              className={`login__eyeButton ${isActive ? "login__eyeButtonActive" : ''}`}
              onClick={onHandleClick}
            />
          <span className="login__error">{errors?.password && errors.password.message}</span>
        </div>
        <Link
          className="login__link"
          rel="stylesheet"
          to="/reset-password"
        >
            
        </Link>
        <button
            type="submit"
            className={`login__submitButton ${!isValid ? "login__submitButtonDisable" : ''}`}
            disabled={!isValid}
          >
            
          </button>
        <div className="login__image_circle"></div>
        <div className="login__image_rectangle"></div>
        <div className="login__image_triangle"></div>
      </form>
    </div>
  );
}

export default Login;
